@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:wght@300;400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Sora&display=swap);
* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	font-family: "Titillium Web", sans-serif;
}



header {
	width: 100%;
	position: fixed;
	display: flex;
	align-items: center;
	height: 50px;
	padding: 0 2rem;
	justify-content: space-between;
}
header h3 {
	font-size: 20px;
	margin: 0 1rem;
	color: white;
	text-decoration: none;
	background: none;
}

nav a {
	font-size: 14px;
	margin: 0 1rem;
	color:white;
	text-decoration: none;
	background: none;
}

nav a:hover {
	color: var(--secondaryColor);
}

header .nav-btn {
	padding: 5px;
	cursor: pointer;
	background: transparent;
	border: none;
	outline: none;
	color: var(--textColor);
	display: none;
	opacity: 0;
	font-size: 1.8rem;
}

nav {
	display: flex;
	align-items: center;
	background: none;
}
.contactbtn{
	font-size: 14px;
	border-radius: 10px;
	border: none;
	padding: 5px 10px;
	background-color: #eee;
}


@media only screen and (max-width: 1024px) {
	header .nav-btn {
		display: block;
		opacity: 1;
	}

	header nav {
		position: fixed;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		grid-gap: 1.5rem;
		gap: 1.5rem;
		background-color: var(--mainColor);
		transition: 1s;
		-webkit-transform: translateY(-100vh);
		        transform: translateY(-100vh);
	}

	header .responsive_nav {
		-webkit-transform: none;
		        transform: none;
	}

	nav .nav-close-btn {
		position: absolute;
		top: 2rem;
		right: 2rem;
	}

	nav a {
		font-size: 1.5rem;
	}
}

